//Font
$SITE_FONT: 'Roboto';

//w: 900
$Font_Black: $SITE_FONT + '-Black'; //Heavy
//w: 800
$Font_ExtraBold: $SITE_FONT + '-ExtraBold';  //Ultra Bold
//w: 700
$Font_Bold: $SITE_FONT + '-Bold';
//w: 600
$Font_SemiBold: $SITE_FONT + '-SemiBold';   //Demi Bold
//w: 500
$Font_Medium: $SITE_FONT + '-Medium';
//w: 400
$Font_Regular: $SITE_FONT + '-Regular'; //Normal
//w: 300
$Font_Light: $SITE_FONT + '-Light';
//w: 200
$Font_ExtraLight: $SITE_FONT + '-ExtraLight';  //Ultra Light
//w: 100
$Font_Thin: $SITE_FONT + '-Thin';

//w: 900
@mixin FontBlack($font: $Font_Black) {
  font-family: $font, sans-serif;
  font-weight: 900;
}
//w: 800
@mixin FontExtraBold($font: $Font_ExtraBold) {
  font-family: $font, sans-serif;
  font-weight: 800;
}
//w: 700
@mixin FontBold($font: $Font_Bold) {
  font-family: $font, sans-serif;
  font-weight: 700;
}
//w: 600
@mixin FontSemiBold($font: $Font_SemiBold) {
  font-family: $font, sans-serif;
  font-weight: 600;
}
//w: 500
@mixin FontMedium($font: $Font_Medium) {
  font-family: $font, sans-serif;
  font-weight: 500;
}
//w: 400
@mixin FontRegular($font: $Font_Regular) {
  font-family: $font, sans-serif;
  font-weight: 400;
}
//w: 300
@mixin FontLight($font: $Font_Light) {
  font-family: $font, sans-serif;
  font-weight: 300;
}
//w: 200
@mixin FontExtraLight($font: $Font_ExtraLight) {
  font-family: $font, sans-serif;
  font-weight: 200;
}
//w: 100
@mixin FontThin($font: $Font_Thin) {
  font-family: $font, sans-serif;
  font-weight: 100;
}

.title {
  color: $title_font_color;
}